<script setup>
import {
  ref,
  reactive,
  onBeforeMount,
  watchEffect,
  computed,
  watch,
} from 'vue';
import assetsBanner from './components/assetsBanner.vue';
import income from './components/income.vue';
import inviteBox from './components/inviteBox.vue';
import inviteRecord from './components/inviteRecord.vue';
import incomeRecord from './components/incomeRecord.vue';
import bindParent from './components/bindParent.vue';
import inviteFriend from './components/inviteFriend.vue';
import { NModal } from 'naive-ui';
import { commonStore } from '@/store/common';
import { swapStore } from '@/store/swap';
import { getProfitList } from '@/http/api';
import { balanceUtil } from '@/utils';
import { useI18n } from 'vue-i18n';
const common_store = commonStore();
const swap_store = swapStore();
const { t } = useI18n();
const userInfo = computed(() => common_store.userInfo);
const balance = computed(() => swap_store.balacne);
const data = reactive({
  show: false,
  name: '',
  list: [],
  loading: true,
  finished: false,
  page: 1,
  bindIng: false,
  recordShow: false,
  total: 0,
  inviteShow: false,
});
const columns = [
  {
    title: t('Account.nav.one'),
    render(row) {
      if (row.type_ == '0') {
        return t('Account.nav.three');
      } else if (row.type_ == '2') {
        return t('Account.nav.seven');
      } else {
        return t('Account.nav.four');
      }
    },
  },
  {
    title: t('Account.nav.two'),
    render(row) {
      return Number(balanceUtil(row.value)).toFixed(2);
    },
  },
  {
    title: t('time'),
    render(row) {
      getFullTime(row.time);
    },
  },
];
const showPopup = (name) => {
  data.name = name;
  data.show = !data.show;
};
const bindFn = async (address) => {
  data.bindIng = true;
  try {
    const res = await common_store.marketMethods
      .bind(address)
      .send({ from: common_store.address });
    sessionStorage.removeItem('parent');
    window.$message.success(t('Account.bindSuccess'));
  } catch (error) {
    window.$message.error(error);
  }
  common_store.getUserInfo();
  data.show = false;
  data.bindIng = false;
};
const getList = () => {
  getProfitList(common_store.address, data.page - 1).then((res) => {
    data.list = res.data.results;
    data.total = res.data.count;
  });
};
watch(
  () => data.page,
  () => {
    getList();
  }
);
onBeforeMount(async () => {
  if (!swap_store.tokenEmpower) {
    await swap_store.getTokenMethods();
  }
  if (common_store.address) {
    getList();
  }
});
watch(
  () => common_store.address,
  () => {
    data.page = 1;
    data.list = [];
    data.loading = true;
    data.finished = false;
    getList();
  }
);
watchEffect(() => {
  if (common_store.address) {
    swap_store.getBalance();
    common_store.initMarketWeb3();
  }
});
</script>
<template>
  <div class="account">
    <assets-banner :assets="balance"></assets-banner>
    <div class="flex_between">
      <income
        :total="balanceUtil(userInfo.userinfo_.nftprofit)"
        :income="balanceUtil(userInfo.userinfo_.extprofit)"
      ></income>
      <invite-box
        :total="userInfo.sons_.length"
        @showPopup="showPopup"
        :parent="userInfo.userinfo_.superadd"
        @inviteShow="data.inviteShow = !data.inviteShow"
      ></invite-box>
    </div>
    <div class="allFlex">
      <invite-record
        :timeArr="userInfo.userinfo_.sonregtime"
        :sons="userInfo.sons_"
      ></invite-record>
      <div class="income-record accountBg">
        <income-record :list="data.list"></income-record>
        <n-pagination
          v-model:page="data.page"
          :item-count="data.total"
          page-size="20"
          v-if="data.total > 0"
          class="page-btn"
        />
      </div>
    </div>
    <n-modal v-model:show="data.show">
      <bind-parent
        @closeFn="data.show = !data.show"
        @bindParent="bindFn"
        :check="data.bindIng"
        v-show="data.name == 'bind'"
      >
      </bind-parent>
    </n-modal>
    <n-modal v-model:show="data.inviteShow">
      <invite-friend @hide="data.inviteShow = !data.inviteShow"></invite-friend>
    </n-modal>
  </div>
</template>

<style lang="scss" scoped>
.account {
  padding: 25px 0;
  width: 65vw;
  min-width: 900px;
  max-width: 1200px;
  margin: 0 auto;
}
.income-record {
  width: calc(50% - 15px);
  padding: 10px 25px;
  border-radius: 15px;
  box-sizing: border-box;
  min-height: 250px;
  border: 1px solid #627391;
}
.page-btn {
  margin-top: 15px;
}
</style>
