<script setup>
import { ref, reactive, toRefs } from 'vue';
import { useRouter } from 'vue-router';
import { useI18n } from 'vue-i18n';
const router = useRouter();
const { t } = useI18n();
const props = defineProps({
  total: Number,
  parent: String,
});
const { parent } = toRefs(props);
const emit = defineEmits(['showPopup', 'inviteShow']);
const toInvite = () => {
  if (parent.value.indexOf('0x00000000') >= 0) {
    window.$message.warning(t('Account.parentAlert'));
    return;
  }
  emit('inviteShow');
};
</script>
<template>
  <ul class="flex_between income accountBg">
    <li class="f1 flex_center">
      <!-- <span class="greyColor font-One">{{ $t('Account.parent') }}</span> -->
      <p
        class="Wcolor font-Eight Bold"
        v-if="parent && parent.indexOf('0x00000') < 0"
      >
        {{ parent.slice(0, 6) }}...{{ parent.slice(-4) }}
      </p>
      <p
        v-else
        class="orangeColor font-Eight hand"
        @click="$emit('showPopup', 'bind')"
      >
        {{ $t('Account.bindBtn') }}
      </p>
    </li>
    <li class="flex_between f1">
      <div>
        <span
          class="darkColor font-Two"
          :class="[$i18n.locale == 'EN' && 'en_text']"
          >{{ $t('Account.invitePerson') }}</span
        >
        <p class="Wcolor font-Eight">{{ total }}</p>
      </div>

      <n-button type="primary" @click="toInvite">{{
        $t('Account.invite')
      }}</n-button>
    </li>
  </ul>
</template>

