<script setup>
import { ref, reactive } from 'vue';
import useClipboard from 'vue-clipboard3';
import { commonStore } from '@/store/common';
import QrcodeVue from 'qrcode.vue';
import { useI18n } from 'vue-i18n';
defineEmits(['hide']);
const { t } = useI18n();
const { toClipboard } = useClipboard();

const common_store = commonStore();
const address = window.location.origin + '/#/?code=' + common_store.address;
const copyLink = async () => {
  try {
    await toClipboard(address);
    window.$message.success(t('copySuccess'));
  } catch (e) {
    window.$message.danger(t('copyError'));
  }
};
</script>
<template>
  <div class="invite-box">
    <img
      src="@assets/commonImg/close_icon.png"
      alt=""
      class="close_icon hand"
      @click="$emit('hide')"
    />
    <ul class="link allFlex column itCenter">
      <li class="Wcolor font-Eight Bold link_title">
        {{ $t('Account.inviteTitle') }}
      </li>
      <li class="link_code Wbg flex_center">
        <qrcode-vue :value="address" :size="178" level="H" />
      </li>
      <li class="Bold center">
        <span class="black Wcolo font-Four"
          >{{ $t('Account.code') }} {{ address.slice(0, 10) }}...{{
            address.slice(-4)
          }}</span
        >
      </li>
      <li class="link-btn">
        <n-button type="primary" block @click="copyLink">{{
          $t('Account.copy')
        }}</n-button>
      </li>
    </ul>
  </div>
</template>

